* {
  margin: 0;
  list-style: none;
}

.recipe {
  border-radius: 10px;
  box-shadow: 0px 10px 15px black;
  margin: 20px;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 55%;
  background: white;
  padding: 0;
}
.recipe:hover {
  background: lightgrey;
}
.recipe h1 {
  width: 50%;
}

.lien {
  text-decoration: none;
  color: black;
}

img {
  height: 100px;
  width: 100px;
  border-radius: 20px;
}
